import { useLocation } from 'react-router';
import styles from './ingredient-details.module.css';
import { useSelector } from 'react-redux';
import React, { useEffect, useState } from 'react';
import { getIngredients } from '../../utils/api';
import { getModalState } from '../../services/modal/reducer';

export const IngredientDetails = () => {

   const { modalProps } = useSelector(getModalState);

    const location = useLocation();
    let background = location.state && location.state.background;

    const [ingredient, setIngredient] = useState({});

    useEffect(() => {
        background = location.state && location.state.background;
        if (Object.keys(modalProps).length !== 0) { //   modalState  ,  
            setIngredient(modalProps);
        } else { //   ,   
            const ingredientId = location.pathname.split('/')[2]; //  id   url
            getIngredients()
            .then(res => {
                const currentIngredient = res.data.find(ingredient => ingredient._id === ingredientId); //   id
                setIngredient(currentIngredient);
            });
        }
    }, [location]);

    return (
        //     
        <div className={background ? styles.modal: styles.main}>
            <h2 className={`text text_type_main-large mt-10 ml-10 ${styles.title}`}> </h2>
            <div className={`mb-15 ${styles.about}`}>
                <img className='mb-4' src={ingredient.image_large} alt={ingredient.name}/>
                <p className='mb-8 text text_type_main-medium'>{ingredient.name}</p>
                <ul className={styles.ul}>
                    <li className={`mr-5 ${styles.li}`}>
                        <p className='text text_type_main-default text_color_inactive mb-2'>,</p>
                        <p className='text text_type_digits-default text_color_inactive'>{ingredient.calories}</p>
                    </li>
                    <li className={`mr-5 ${styles.li}`}>
                        <p className='text text_type_main-default text_color_inactive mb-2'>, </p>
                        <p className='text text_type_digits-default text_color_inactive'>{ingredient.proteins}</p>
                    </li>
                    <li className={`mr-5 ${styles.li}`}> 
                        <p className='text text_type_main-default text_color_inactive mb-2'>, </p>
                        <p className='text text_type_digits-default text_color_inactive'>{ingredient.fat}</p>
                    </li>
                    <li className={styles.li}>
                        <p className='text text_type_main-default text_color_inactive mb-2'>, </p>
                        <p className='text text_type_digits-default text_color_inactive'>{ingredient.carbohydrates}</p>
                    </li>
                </ul>
            </div>
        </div>
    );
};