import { Link, Outlet, useLocation } from 'react-router-dom';
import { logout } from '../../services/user/action';
import styles from './profile.module.css';
import { useDispatch } from 'react-redux';
import { RoutePathname } from '../../utils/constants';
import React from 'react';

export const Profile = () => {
    const dispatch = useDispatch();
    const location = useLocation();

    return (
        <div className={styles.main}>
            <div className={`mr-15 mt-30 ${styles.content}`}>
                <nav className={`mb-20 ${styles.navigation}`}>
                    <Link 
                    to='/profile'
                    className={`text text_type_main-medium pt-4 pr-10 pb-4 pl-10 ${location.pathname === RoutePathname.profilePage ? styles.tab_active : styles.tab}`}></Link>
                    <Link 
                    to='/profile/orders' 
                    className={`text text_type_main-medium pt-4 pr-10 pb-4 pl-10 ${location.pathname === '/profile/orders' ? styles.tab_active : styles.tab}`}>  </Link>
                    <a onClick={() => dispatch(logout())} className={`text text_type_main-medium pt-4 pr-10 pb-4 pl-10 ${ styles.tab }`}></a>
                </nav>
                <p className={`text text_type_main-small text_color_inactive pt-4 pb-4 pl-10 ${styles.text_color_dark}`}>         </p>
            </div>
            <Outlet/>
        </div>
    );
};